import {
  GET_VIDEOGAMES,
  GET_VIDEOGAME_ID,
  GET_VIDEOGAME_NAME,
  GET_GENRES,
  FILTER_BY_GENRE,
  FILTER_BY_SOURCE,
  ORDER_BY_NAME,
  ORDER_BY_RATING,
} from "./action-types";
const initialState = {
  allVideogames: [],
  videogames: [],
  videogameDetail: [],
  genres: [],
};

const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_VIDEOGAMES:
      return { ...state, allVideogames: action.payload };
    case GET_VIDEOGAME_ID:
      return { ...state, videogameDetail: action.payload };
    case GET_VIDEOGAME_NAME:
      return { ...state, videogames: action.payload };
    case GET_GENRES:
      return { ...state, genres: action.payload };

    //Filters
    case FILTER_BY_GENRE:
      const allGames = state.allVideogames;
      const filteredGames = allGames.filter((game) =>
        game.genres.includes(action.payload)
      );
      return { ...state, videogames: filteredGames };

    case FILTER_BY_SOURCE:
      if (action.payload === "All")
        return { ...state, videogames: state.allVideogames };
      else if (action.payload === "api")
        return {
          ...state,
          videogames: state.allVideogames.filter(
            (game) => !game.hasOwnProperty("created")
          ),
        };
      else
        return {
          ...state,
          videogames: state.allVideogames.filter((game) =>
            game.hasOwnProperty("created")
          ),
        };

    //Order
    case ORDER_BY_NAME:
      const isAscending = action.payload === "asc";
      const sortedGames = [...state.videogames].sort((a, b) => {
        const titleA = a.title.toLowerCase();
        const titleB = b.title.toLowerCase();
        if (titleA < titleB) return isAscending ? -1 : 1;
        if (titleA > titleB) return isAscending ? 1 : -1;
        return 0;
      });
      return {
        ...state,
        recipes: sortedGames,
      };

    case ORDER_BY_RATING:
      const allVideogamesCopy = [...state.allVideogames];
      return {
        ...state,
        videogames:
          action.payload === "high-low"
            ? allVideogamesCopy.sort((a, b) => a.rating - b.rating)
            : allVideogamesCopy.sort((a, b) => b.rating - a.rating),
      };

    default:
      return { ...state };
  }
};

export default rootReducer;
