import style from "./Estilos/Home.module.css";
import Cards from "../components/Cards";
import { useEffect } from "react";
import { useDispatch } from "react-redux";
import { getVideogames } from "../redux/actions";
import { useSelector } from "react-redux";

function Home() {
  //Hooks
  const allVideogames = useSelector((state) => state.allVideogames);
  console.log(allVideogames);
  const dispatch = useDispatch();
  useEffect(() => {
    //cuando el componente se monta
    dispatch(getVideogames);
  }, [dispatch]);

  return (
    <div className={style.home}>
      <div className={style.filtros}>
        <div>
          <h3 className={style.title}>Genres</h3>
          <select className={style.genres}>
            <option value="All">All</option>
            <option value="Action">Action</option>
            <option value="Indie">Indie</option>
            <option value="Adventure">Adventure</option>
            <option value="RPG">RPG</option>
            <option value="Strategy">Strategy</option>
            <option value="Shooter">Shooter</option>
            <option value="Casual">Casual</option>
            <option value="Simulation">Simulation</option>
            <option value="Puzzle">Puzzle</option>
            <option value="Arcade">Arcade</option>
            <option value="Platformer">Platformer</option>
            <option value="Massively Multiplayer">Massively Multiplayer</option>
            <option value="Racing">Racing</option>
            <option value="Sports">Sports</option>
            <option value="Fighting">Fighting</option>
            <option value="Family">Family</option>
            <option value="Board Games">Board Games</option>
            <option value="Educational">Educational</option>
            <option value="Card">Card</option>
          </select>
        </div>
        <div>
          <h3 className={style.title}>Source</h3>
          <select className={style.source}>
            <option value="All">All</option>
            <option value="api">Api</option>
            <option value="created">Created</option>
          </select>
        </div>

        <div>
          <h3 className={style.title}>Name</h3>
          <select className={style.alpha}>
            <option value="asc">A-Z</option>
            <option value="des">Z-A</option>
          </select>
        </div>

        <div>
          <h3 className={style.title}>Rating</h3>
          <select className={style.rating}>
            <option value="high-low">High-Low</option>
            <option value="low-high">Low-High</option>
          </select>
        </div>
      </div>
      <Cards allVideogames={allVideogames} />
    </div>
  );
}

export default Home;
