import { Link } from "react-router-dom";
import SearchBar from "./SearchBar";
import style from "./Estilos/NavBar.module.css";
function NavBar() {
  return (
    <div className={style.navBar}>
      <SearchBar />
      <div>
        <img
          src="https://upload.wikimedia.org/wikipedia/commons/thumb/b/b1/Rick_and_Morty.svg/1200px-Rick_and_Morty.svg.png"
          alt="Logo"
          className={style.logo}
        />
      </div>
      <div>
        <Link to="/home">
          <button className={style.homeButton}>
            <span>Home</span>
          </button>
        </Link>
      </div>
      <div>
        <Link to="/form">
          <button className={style.createButton}>
            <span>Create</span>
          </button>
        </Link>
      </div>
    </div>
  );
}

export default NavBar;
